.app-body{
  content: '';
  height: 100%;
  width: 100%;
}
#home, #projects, #about, #achievements{
  transition: all .8s ease-in-out;
  min-width: calc(100% - 160px);
  height: 100%;
  right: 0;
  overflow-y: auto;
  overflow-x: hidden;
  margin-top: -250%;
  position: absolute;
  will-change: margin-top;
  z-index: 1;
 
  margin-left: 156px;
  &:target{
    margin-top: 0%;

  }
}
