.project-item{
  background: #3F4143;
min-height: 500px;
border-radius: 5px;
padding-top: 13%;
padding-left: 13%;
&__front{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;

}
&__header{
  display: flex;
  justify-content: space-between;
  padding-right: 13%;
}
&__title{
  text-transform:uppercase;
  color:#64F4AB;
}
&__tehnologies{
  display: flex;
  gap: 5px;
  & span{
    padding: 5px;
    font-size: 0.7rem;
    color: white;
    background: #2D3037;
  }
}


&__links{
  position: absolute;
  right: 0;
  bottom: 0;
  padding: 10px;
  display: flex;
  // flex-direction: column;
  gap: 10px;
  font-size: 2rem;
  cursor: pointer;
 a {
  color: white;

 }
}
&__photo{
  position: relative;
  flex: 1 1 auto;
  margin-top: 5%;
  width: 100%;

& img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}}

&__info {

}
}

.project-item{
  border: 1px solid #64F4AB;
  box-shadow: 1px 1px 2px #64F4AB;                
  
&__photo{
  display: inline-block;
  position: relative;
  overflow: hidden;

& img {
  display: block;
  position: relative;
  transform: scaleY(1);
  transition: all 0.7s ease-in-out;
}}
&:hover img {
  transform: scale(10);
  opacity: 0;
}

&__info {
  position: absolute;
  overflow: hidden;
  top: 0;
  left: 0;
  background-color: #181B20;
  transition: all 0.5s linear;
  opacity: 0;
  height: 100%;
  width: 100%;
  .project-item:hover &{
    opacity: 1;
  }
  & h3 {
    margin: 0 10px;
    padding: 10px;
    border-bottom: 1px solid #d5d5d5;
    text-transform: uppercase;
    text-align: center;
    font-size: 1em;
    font-weight: 700;
    color:#64F4AB;
  
    transform: scale(0);
    transition: all 0.5s linear;
    opacity: 0;
  }
  
  & p {
    padding: 10px;
    font-size: 1em;
    line-height: 1.2;
    font-weight: normal;
    text-align: center;
    color:#64F4AB;

    transform: scale(0);
    transition: all 0.5s linear;
    opacity: 0;
  }
.project-item:hover & h3,
.project-item:hover & p {
  transform: scale(1);
  opacity: 1;
}
}
}

