.app-button {
  height: 50px;
  background: #2D2E32;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 5px;
  border: 1px solid #64F4AB;
cursor: pointer;
  & span {
    display: block;
    content: '';
    height: 3px;
    width: 20%;
  background: #64F4AB;
  }
}