// Configuration parameters
// Define width of individual slide (% or px)
$slide-width: 42%;

// Define gap between the slides
$slide-separation: 40px;

$distance: 1000px;

.achievements{
  margin: 0;
  height: 100%;
  width: 100%;
  background-color: gray;
  &__show-img{
    position: absolute;
    height: 100%;
    width: 100%;
    right: 50%;
    transform: translate(50% );
    z-index: 1000;
    img {
      height: 100%;
      width: 100%;
      object-fit:contain;
    }
  }
}
.achievements-carousel{
  height: 100%;

  display: flex;
  justify-content: space-between;
  flex-direction: column;

  perspective: $distance;
  overflow: hidden;
  height: 100%;

  &__scene {
    width: $slide-width;
    margin: 10% auto 0 auto;
    transform-style: preserve-3d;
    transition: transform 0.5s;
  }
  &__item {
    width: 100%;
    box-sizing: border-box;
    padding: 0 ($slide-separation / 2) 0 ($slide-separation / 4);
    backface-visibility: hidden;
    &:not(:first-of-type) {
      position: absolute;
      left: 0;
      top: 0;
    }
  }
  
  &__btns {
    display: flex;
    justify-content: space-around;
    width: 100%;
    margin: 20px auto 0 auto;
  }

  &__prev, &__next {
    border: 2px solid #64F4AB;
    color: #64F4AB;
    width: 100px;
    height: 50px;
    background: rgba(0, 0, 0, 0.568);
    text-align: center;
    line-height: 50px;
    box-sizing: border-box;
    cursor: pointer;
    transition: all 0.3s;
    &:hover {
      color: #ffffff;
      background-color: #2f2f2f;
    }
    
   &:first-child {
      margin-right: 10px;
    }
  }
  
  &__count{
    line-height: 50px;
    font-weight: bold;
    font-size: 1.5rem;
    color:#768DE3;
    background: rgba(0, 0, 0, 0.788);
  }
}